.propiedad {
  background-color: #FFFFFF;
  /* Bucles en SASS
      la sintaxis es la siguiente.

      - indica una variable '$i como iterador, 
      - indica un intervalo que quieres que se ejecute tu codigo, en este ejemplo del 1 hasta el 6 'from 1 through 6'
  */
}
.propiedad:nth-child(1) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_1.jpg");
}
.propiedad:nth-child(2) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_2.jpg");
}
.propiedad:nth-child(3) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_3.jpg");
}
.propiedad:nth-child(4) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_4.jpg");
}
.propiedad:nth-child(5) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_5.jpg");
}
.propiedad:nth-child(6) .propiedad__header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_6.jpg");
}
.propiedad__header {
  /* aspect-ratio 
      se utiliza para controlar el tamaño y la relación de aspecto de un elemento de diseño, como una imagen o un contenedor. La relación de aspecto es la relación entre la anchura y la altura de un elemento, y es comúnmente utilizada para asegurarse de que un elemento mantenga sus proporciones correctas, especialmente cuando se redimensiona la ventana del navegador o el dispositivo.

          aspect-ratio: ancho / alto;

      ancho y alto: Los valores numéricos que representan la relación de aspecto deseada del elemento. Por ejemplo, si deseas que el elemento tenga una relación de aspecto 16:9, escribirías 16 / 9.
  */
  aspect-ratio: 16/10;
  background-size: 100% 210%;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
}
@media (min-width: 768px) {
  .propiedad__header {
    background-size: auto 150%;
  }
}
@media (min-width: 1024px) {
  .propiedad__header {
    background-size: auto 130%;
  }
}
.propiedad__titulo {
  padding: 1rem;
  color: #FFFFFF;
  margin: 0;
  font-size: 2.4rem;
}
.propiedad__info {
  padding: 2rem;
}
.propiedad__texto {
  margin-bottom: 0;
}
