}
@media (min-width: 768px) {
  .propiedad__link {
    width: auto;
  }
}
@media (min-width: 1024px) {
  .propiedad__link {
    font-size: 2rem;
  }
}
.propiedad__link:hover {
  background-color: #283142;
}

